import { query } from 'lit/decorators.js';

import { Base } from './base';
import type { ZwcTemplate } from '../shared/zwc-template';

export abstract class Template extends Base {
  /**
   * Declare in extended class
   * decorated data property
   * @state()
   * data: string = '';
   */
  abstract data: string;

  @query('zwc-template')
  content!: ZwcTemplate;

  // On init
  public connectedCallback(): void {
    super.connectedCallback();
    this.content?.addEventListener('change', this.handleChange.bind(this));
  }

  // On destroy
  public disconnectedCallback(): void {
    super.disconnectedCallback();
    this.content?.removeEventListener('change', this.handleChange);
  }

  protected handleChange(ev: Event): void {
    this.data = (ev as CustomEvent).detail as string;
  }
}
